.recipe-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;
  max-width: 1200px;
  margin: 40px auto;
}

.recipe-list .card {
  background-color: white;
  color: #333;
  padding: 1rem;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
  transition: all 0.5s ease;
  position: relative;
}

.recipe-list .card:hover {
  transform: scale(1.05);
}

.recipe-list .card h3 {
  color: #555;
  margin-bottom: 6px;
}

.recipe-list .card p {
  color: #999;
  font-size: 0.9em;
}

.recipe-list .card .card-details {
  color: #555;
  font-size: 0.7em;
  margin: 20px 0;
  line-height: 1.5em;
}

.recipe-list .card .read-more {
  color: #555;
  text-decoration: none;
  display: block;
  background: #e2e2e2;
  font-size: 0.9em;
  width: 100px;
  text-align: center;
  width: 120px;
  padding: 8px;
  border-radius: 4px;
  margin: 20px auto 0;
}
